import React from 'react'
import { Route, Link } from 'react-router-dom'
import ReactGA from 'react-ga';
import Home from '../home'
import Uploaded from '../uploaded'
import logo from '../../images/logo.png'
import Terms from '../../containers/terms'



const currentYear = new Date().getFullYear()

class App extends React.Component {

  constructor() {
    super()
      ReactGA.initialize('UA-87671507-3', {
        debug: true,
        titleCase: false,
        siteSpeedSampleRate: 100
      });
  }
  render() {
    return (
        <div>
        <header className="navbar navbar-light bg-light">
          <Link to="/" className="navbar-brand" href="#">
            <img src={logo} width="30" height="30" className="d-inline-block align-top" alt="mp3towav" style={{margin: "0 15px"}}/>
            MP3TOWAV
          </Link>
          </header>
    
        <main>
          <Route exact path="/" component={Home} />
          <Route exact path="/uploaded" component={Uploaded} />
          <Route exact path="/terms" component={Terms} />
        </main>
    
    
        <footer>
          <div className="col-md-12 flex-center">
              <Link to="/" style={{fontFamily: "Main", color: "black"}}><img height="30px" src={logo} alt="wavtomp3" /> MP3TOWAV copyright {currentYear}</Link>
          </div>
        </footer>
    </div>
    )
  }
}

export default App
